#routes {
  position: absolute;
  top: 100vh;
  width: 100%;
  /* min-width: 1100px; */
}

.route {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  min-height: 100vh;
  width: 100%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  -ms-flex-pack: center;
      justify-content: center;
  text-align: center;
}

.text {
  color: white;
}

.title {
  font-size: 42px;
  margin: 30px;
}

.subtitle {
  font-size: 22px;
  margin: 10px 5px;
}

.info {
  font-size: 18px;
  font-style: oblique;
  margin: 0 10px;
  text-align: left;
  white-space: pre-wrap;
}

#overlay {
  z-index: 1;
  opacity: 0;
  position: absolute;
  height: 100vh;
  width: 100%;
}



